@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:rgba(0,0,0,0.9);font-family:"Open Sans", sans-serif;color:rgba(255,255,255,0.9)}*{box-sizing:border-box;margin:0;padding:0}.header,.footer{display:flex;flex-flow:row wrap;justify-content:center;align-items:center}.header{margin:4rem}.footer{margin:2rem}.header{display:flex;flex-flow:row wrap;justify-content:space-between}

table,caption,tbody,tfoot,thead,tr,th,td{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}table{border-collapse:collapse;border-spacing:0}::-webkit-scrollbar{width:5px}::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(0,0,0,0.3)}::-webkit-scrollbar-thumb{box-shadow:inset 0 0 6px rgba(255,255,255,0.5);border-radius:100px}.table{width:100%;table-layout:fixed;border:none;border-collapse:collapse;overflow:scroll}.table .table-row{background-color:rgba(153,153,153,0.1);transition:all 0.2s ease-out}.table .table-row:hover{background-color:rgba(0,0,0,0.9);box-shadow:0 0 0 2px #444,0px 0px 10px rgba(255,255,255,0.6)}.table .table-row:nth-of-type(even){background:rgba(231,196,104,0.1)}.table .table-row:nth-of-type(even):hover{background-color:rgba(26,26,26,0.9)}.table .table-head{background-color:rgba(255,255,255,0.1);text-align:left;border-bottom:3px solid #4fc0d2}.table .table-body{overflow:scroll}.table td,.table th{text-align:right;padding:1rem 0.4rem}.table td{border-top:1px solid rgba(0,0,0,0.1)}.table td:first-of-type{text-align:left}.table td:nth-of-type(2){text-align:left}.table .first-column{border-right:3px solid rgba(79,192,210,0.5);display:flex;flex-flow:row wrap;align-items:center}.table .first-column h3{margin:0rem 0.1rem;font-weight:bold;align-self:baseline}.table .first-column span{font-size:smaller;align-self:flex-end}.table .high-style{color:#52d29a}.table .low-style{color:#eb7655}

.pagination {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}
.pagination .page {
  font-size: 14px;
  padding: 0.9rem;
  margin: 0.4rem;
  border-radius: 100%;
  line-height: 0.9rem;
  background: rgba(79, 192, 210, 0.8);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: ease-in-out 0.3s all;
  box-shadow: 0 0 0 2px #444, 0px 0px 10px rgba(255, 255, 255, 0.6);
}
.pagination .page:hover,
.pagination .page-active {
  -webkit-text-fill-color: none;
  border-color: #fff;
  background-clip: border-box;
  -webkit-text-fill-color: #fff;
}

.search{position:relative;width:60px;height:60px;border-radius:60px;transition:0.5s;box-shadow:0 0 0 2px #444,0px 0px 10px rgba(255,255,255,0.6);overflow:hidden;background-color:rgba(0,0,0,0.9)}.search .icon{position:absolute;top:0;left:0;width:60px;height:60px;background-color:rgba(0,0,0,0.9);border-radius:60px;display:flex;justify-content:center;align-items:center;z-index:1000;transition:0.3s ease-in-out}.search .icon::before{content:"";position:absolute;width:15px;height:15px;border-radius:50%;border:3px solid #4fc0d2;-webkit-transform:translate(-4px, -4px);transform:translate(-4px, -4px)}.search .icon::after{content:"";position:absolute;width:3px;height:12px;background-color:#4fc0d2;-webkit-transform:translate(6px, 6px) rotate(315deg);transform:translate(6px, 6px) rotate(315deg);cursor:pointer;overflow:hidden}.search.active{width:360px}.search .input{position:relative;width:14rem;height:60px;left:60px;display:flex;justify-content:center;align-items:center;overflow:hidden}.search .input input{position:absolute;top:0%;width:100%;height:100%;border:none;outline:none;font-size:18px;background:transparent;color:rgba(255,255,255,0.9)}.clear{position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);width:15px;height:15px;right:3.5rem;cursor:pointer;display:flex;justify-content:center;align-items:center;cursor:pointer}.clear::before{position:absolute;content:"";width:1px;height:15px;background:#333;-webkit-transform:rotate(45deg);transform:rotate(45deg)}.clear::after{position:absolute;content:"";width:1px;height:15px;background:#333;-webkit-transform:rotate(315deg);transform:rotate(315deg)}.submit{right:0rem;position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);padding:1.2rem 1rem;color:#287dfc;cursor:pointer;transition:0.4s all ease-in;border-radius:10rem}.submit:hover,.clear:hover{background-color:rgba(79,192,210,0.2)}.icon:hover,.submit:hover .submit-icon{-webkit-transform:rotate(45deg);transform:rotate(45deg)}

